.ai-analytics-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: #f8fafc;
  min-height: 100vh;
}
.ai-analytics-page .analytics-content .content-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}
.ai-analytics-page .analytics-content .content-grid .left-column {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.ai-analytics-page .analytics-content .content-grid .right-column {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.ai-analytics-page .loading-state .loading-skeleton {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 2rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-icon {
  width: 4rem;
  height: 4rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 1rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content .skeleton-line {
  height: 1rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.25rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content .skeleton-line.w-3-4 {
  width: 75%;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content .skeleton-line.w-1-2 {
  width: 50%;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content .skeleton-line.w-full {
  width: 100%;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-content .skeleton-line.w-2-3 {
  width: 66.666667%;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-body {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.ai-analytics-page .loading-state .loading-skeleton .skeleton-body .skeleton-line {
  height: 0.875rem;
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.25rem;
}
.ai-analytics-page .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
}
.ai-analytics-page .empty-state .empty-icon {
  color: #d1d5db;
  margin-bottom: 1.5rem;
}
.ai-analytics-page .empty-state .empty-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.75rem 0;
}
.ai-analytics-page .empty-state .empty-description {
  font-size: 1rem;
  color: #6b7280;
  margin: 0 0 2rem 0;
  line-height: 1.6;
  max-width: 32rem;
}
.ai-analytics-page .empty-state .generate-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.ai-analytics-page .empty-state .generate-button:hover {
  background: linear-gradient(135deg, #1d4ed8 0%, #1e40af 100%);
  transform: translateY(-1px);
}
.ai-analytics-page .empty-state .generate-button:active {
  transform: translateY(0);
}
.ai-analytics-page .empty-state .generate-button svg {
  width: 1rem;
  height: 1rem;
}
.ai-analytics-page .error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 2rem;
  text-align: center;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
}
.ai-analytics-page .error-state .error-icon {
  color: #ef4444;
  margin-bottom: 1rem;
}
.ai-analytics-page .error-state .error-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.ai-analytics-page .error-state .error-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 1.5rem 0;
  line-height: 1.5;
}
.ai-analytics-page .error-state .retry-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.ai-analytics-page .error-state .retry-button:hover {
  background: linear-gradient(135deg, #1d4ed8 0%, #1e40af 100%);
  transform: translateY(-1px);
}
.ai-analytics-page .error-state .retry-button:active {
  transform: translateY(0);
}
.ai-analytics-page .error-state .retry-button svg {
  width: 1rem;
  height: 1rem;
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
@media (max-width: 1024px) {
  .ai-analytics-page .analytics-content .content-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}
@media (max-width: 768px) {
  .ai-analytics-page {
    padding: 1rem;
  }
  .ai-analytics-page .analytics-content .content-grid {
    gap: 1rem;
  }
  .ai-analytics-page .loading-state .loading-skeleton {
    padding: 1rem;
  }
  .ai-analytics-page .loading-state .loading-skeleton .skeleton-header .skeleton-icon {
    width: 3rem;
    height: 3rem;
  }
  .ai-analytics-page .empty-state {
    padding: 2rem 1rem;
  }
  .ai-analytics-page .empty-state .empty-title {
    font-size: 1.25rem;
  }
  .ai-analytics-page .empty-state .empty-description {
    font-size: 0.875rem;
  }
  .ai-analytics-page .empty-state .generate-button {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
  .ai-analytics-page .error-state {
    padding: 2rem 1rem;
  }
  .ai-analytics-page .error-state .error-title {
    font-size: 1.125rem;
  }
  .ai-analytics-page .error-state .retry-button {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
}
